import { ReportHandler } from 'web-vitals';

export const reportWebVitals = (onPerfEntry?: ReportHandler) => {
  if (onPerfEntry && onPerfEntry instanceof Function) {
    import('web-vitals').then(({
      getCLS, getFID, getFCP, getLCP, getTTFB,
    }) => {
      getCLS(onPerfEntry);
      getFID(onPerfEntry);
      getFCP(onPerfEntry);
      getLCP(onPerfEntry);
      getTTFB(onPerfEntry);
    });
  }
};

// Post Web Vitals reports to Foo.software
const ACCOUNT_ID = '6030f7aeb965970024f562d7';
const VITALS_URL = 'https://www.foo.software/api/vitals';

export function postVitals(metric:any) {
  const body = JSON.stringify({
    accountId: ACCOUNT_ID,
    name: metric.name,
    url: 'https://ammar-oker.github.io/',
    value: metric.value,
  });

  return (
    (navigator.sendBeacon && navigator.sendBeacon(VITALS_URL, body))
      || fetch(VITALS_URL, {
        body,
        method: 'POST',
        keepalive: true,
        headers: {
          'Content-Type': 'application/json',
        },
      })
  );
}
