import React, {
  MouseEvent, FormEvent, useState, FC,
} from 'react';
import { createUseStyles } from 'react-jss';

type ModalProps = {
    onClose: Function
}

const useStyles = createUseStyles({
  modal: {
    position: 'fixed',
    top: '50%',
    left: '50%',
    background: '#FFFFFF',
    boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2)',
    width: '95%',
    maxWidth: '400px',
    padding: '1em',
  },
  input: {
    display: 'block',
    margin: '10px 0',
    width: '100%',
    height: '40px',
    fontSize: 'large',
  },
  textArea: {
    display: 'block',
    margin: '10px 0',
    width: '100%',
    height: '100px',
    fontSize: 'large',
  },
  actions: {
    '& button': {
      minWidth: '100px',
      fontSize: 'large',
      '&:not(:first-child)': {
        marginRight: '5px',
      },
    },
  },
  modalWrapper: {
    position: 'fixed',
    height: '100vh',
    width: '100vw',
    top: 0,
    left: 0,
  },
});

const Modal: FC<ModalProps> = (props: ModalProps) => {
  const { onClose } = props;
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const [requestState, setRequestState] = useState('');

  const close = (event: MouseEvent<HTMLElement>) => {
    onClose(event);
  };

  const submitForm = (e:FormEvent) => {
    e.preventDefault();
    setLoading(true);
    const form = e.target as HTMLFormElement;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader('Accept', 'application/json');
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        setLoading(false);
        setRequestState('SUCCESS');
      } else {
        setRequestState('ERROR');
        setLoading(false);
      }
    };
    xhr.send(data);
  };

  return (
    <div className={classes.modal} onClick={(e) => { e.stopPropagation(); }}>
      <h2>Get in touch</h2>
      <div className="content">
        {'Let\'s work on something great together'
        + ' or just say hi, fill the contact form below or contact on my email '}
        <a href="mailto:oker.ammar@gmail.com">
          oker.ammar@gmail.com
        </a>
        <form
          method="POST"
          action="https://formspree.io/f/xbjpeqeo"
          onSubmit={submitForm}
        >
          <input name="name" className={classes.input} placeholder="name" required />
          <input type="email" name="email" placeholder="email" className={classes.input} required />
          <textarea name="message" className={classes.textArea} placeholder="message" required />
          <div className={classes.actions}>
            <p>
              {
                requestState && (
                  requestState === 'SUCCESS' ? (
                    'Message sent, thanks u ðŸ’™'
                  ) : (
                    'Something went wrong ðŸ˜•'
                  )
                )
              }
            </p>
            <button type="submit" disabled={loading}>
              {
                loading ? (
                  <p className="loading">
                    <span>.</span>
                    <span>.</span>
                    <span>.</span>
                  </p>
                ) : 'Send'
              }
            </button>
            <button type="button" onClick={close}>
              close
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Modal;
