import React, { useState } from 'react';
import { createUseStyles } from 'react-jss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTwitter, faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons';
import { CSSTransition } from 'react-transition-group';
import Modal from './Components/Modal';

const useStyles = createUseStyles({
  wrapper: {
    minHeight: '100vh',
    display: 'flex',
    padding: '0 1em',
  },
  profile: {
    margin: 'auto',
  },
  socialLinks: {
    listStyle: 'none',
    padding: '0',
    margin: '10px 0',
    '& li': {
      display: 'inline-block',
      '&:not(:first-child)': {
        marginLeft: '15px',
      },
      '& button': {
        fontSize: 'medium',
      },
    },
  },
});

function App() {
  const classes = useStyles();
  const [showContactModal, setShowContactModal] = useState(false);

  const toggleContactModal = (val:boolean) => {
    setShowContactModal(val);
  };

  return (
    <>
      <div className={classes.wrapper} onClick={() => { toggleContactModal(false); }}>
        <div className={classes.profile}>
          <h1>Howdy ðŸ¤ </h1>
          <p>
            {'My name\'s\n'}
            <strong>Ammar Oker</strong>
            <br />
            {'I\'m a Javascript guy and I do cool stuff with Node.js & React.js\n'}
          </p>
          <ul className={classes.socialLinks}>
            <li>
              <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/ammar-oker/">
                <FontAwesomeIcon icon={faLinkedin} size="lg" />
              </a>
            </li>
            <li>
              <a target="_blank" rel="noreferrer" href="https://twitter.com/ammar_oker">
                <FontAwesomeIcon icon={faTwitter} size="lg" />
              </a>
            </li>
            <li>
              <a target="_blank" rel="noreferrer" href="https://github.com/ammar-oker">
                <FontAwesomeIcon icon={faGithub} size="lg" />
              </a>
            </li>
            <li>
              <button
                type="button"
                onClick={(e) => {
                  e.stopPropagation();
                  toggleContactModal(true);
                }}
              >
                Get in touch
              </button>
            </li>
          </ul>
        </div>
      </div>
      <CSSTransition
        in={showContactModal}
        timeout={300}
        unmountOnExit
        classNames="modal"
      >
        <Modal onClose={() => { toggleContactModal(false); }} />
      </CSSTransition>
    </>
  );
}

export default App;
